</template>

<script>
  import config from './config'

  export default {

    created() {

      console.log("根实例(created)已载入")

      this.axios.defaults.baseURL = config.apiBaseURL
      this.axios.defaults.headers.common['Authorization'] = localStorage.getItem("token")

      // 自定义 axios 响应拦截器
      this.axios.interceptors.response.use(
        response => {

          let token = response.headers['authorization']
          if (token) {
            localStorage.setItem("token", token)
            this.axios.defaults.headers.common['Authorization'] = localStorage.getItem("token")
          }
          return response
        },
        error => {

          if (error.response) {
            let status = error.response.status

            if (status === 400 || status === 422 || status === 500) {
              this.$dialog.alert({message: error.response.data.message})
            }
            if (status === 401) {
              this.$toast.fail('请先登录');
              this.$router.replace({name: 'Login', query: {return_url: this.$route.path}})
              // this.$router.push({name: 'Login', query: {return_url: this.$route.path}})
            }
          }
          return Promise.reject(error);
        }
      )
    },
  }
</script>

