<template>
  <div :class="['animated', effect]"
       :style="[show ? 'visibility: visible' : 'visibility: hidden']">
    <slot></slot>
  </div>
</template>

<script>
  export default {
    props: {

      // 多少毫秒后开始执行动画
      time: {
        type: Number,
        required: false,
        default: 0,
      },

      // 要使用的动画效果名称, 详见 https://www.cnblogs.com/rlann/p/7192535.html
      effect: {
        type: String,
        required: false,
        default: 'bounceIn',
      },
    },
    data() {
      return {
        show: false
      }
    },
    created() {
      setTimeout(() => {
        this.show = true
      }, Number(this.time))
    },

    methods: {
    }
  }
</script>

