import Vue from 'vue'
import Router from 'vue-router'
import Home from './views/Home.vue'

Vue.use(Router)

export default new Router({
  mode: 'history',
  routes: [
    {
      path: '/---',
      name: 'home',
      component: Home
    },
    {
      path: '/about',
      name: 'About',
      component: () => import('./views/About.vue')
    },
    {
      path: '/alterAvatar',
      name: 'AlterAvatar',
      component: () => import('./views/AlterAvatar.vue')
    },
    {
      path: '/me',
      name: 'Me',
      component: () => import('./views/Me.vue')
    },
    {
      path: '/login',
      name: 'Login',
      component: () => import('./views/Login.vue')
    },
    {
      path: '/',
      name: 'Luck',
      component: () => import('./views/Luck.vue')
    },
  ]
})
